import {addProductAPI, modifyProductAPI, deleteProductAPI, getProductsAPI, getProductAPI} from '../../API/scoreAPI';
import {getCurrentUserAPI} from '../../API/authAPI';

export const addProduct = (product) => {
    return async (dispatch, getState) => {
        try {
            await addProductAPI(product);
            dispatch({type: 'CREAE_PRODUCT_SUCCESS'});
            return true;
        } catch (err) {
            dispatch({type: 'CREATE_PRODUCT_FAILED', err});
        }
    }
}

export const modifyProduct = (product) => {
    return async (dispatch, getState) => {
        try {
            await modifyProductAPI(product);
            dispatch({type: 'MODIFY_PRODUCT_SUCCESS'});
            return true;
        } catch (err) {
            dispatch({type: 'MODIFY_PRODUCT_FAILED', err});
        }
    }
}

export const deleteProduct = (id) => {
    return async (dispatch, getState) => {
        try{
            const userId = getCurrentUserAPI().id;
            await deleteProductAPI(id, userId);
            dispatch({type: 'DELETE_PRODUCT_SUCCESS'});
            return true;
        } catch (err) {
            dispatch({type: 'DELETE_PRODUCT_FAILED', err});
        }
    }
}

export const getProducts = () => {
    return async (dispatch) => {
        const userId = getCurrentUserAPI().id;
        const productList = await getProductsAPI(userId);
        dispatch({type: 'GET_PRODUCTS_SUCCESS', productList});
    }
}

export const getProduct = (id) => {
    return async (dispatch) => {
        const product = await getProductAPI(id);
        dispatch({type: 'GET_PRODUCT_SUCCESS', product});
        return product;
    }
}