import React, {Component} from 'react';
import { Flex, WhiteSpace, Button, WingBlank, List } from 'antd-mobile';
import { getDefaultBaby } from '../../store/actions/babyActions';
import { connect } from 'react-redux';
import {getHabbitRecord } from '../../store/actions/habbitsActions';
import moment from 'moment';
const Item = List.Item;
const Brief = Item.Brief;

class Dashboard extends Component {
    async componentDidMount() {
        if(this.props.getDefaultBaby) {
            const defaultBaby = await this.props.getDefaultBaby();

            this.props.getHabbitRecord(defaultBaby.id);
        }
    }

    goToCreateHabbit = () => {
        this.props.history.push("/habbits");
    }

    goToRecordHabbit = () => {
        this.props.history.push("/habbitRecord");
    }

    render() {
        const {defaultBaby, habbitRecordList} = this.props;
        console.log(habbitRecordList)
        if(!defaultBaby)
            return (
                <div>Loading...</div>
            )

        let habbitRecordListUI = "";    
        if(habbitRecordList && habbitRecordList.length) {
            habbitRecordListUI = habbitRecordList.map(habbitRecord => {
                console.log("created", habbitRecord.createdAt)
                return (
                    <List key={habbitRecord.id}>
                        <Item multipleLine extra={(habbitRecord.habbit.habbitType == 0 ? "+" : "-") + habbitRecord.habbit.reward }>
                            {habbitRecord.habbit.description} <br/>
                            <Brief>{moment(habbitRecord.createdAt).calendar()}</Brief>
                        </Item>
                    </List>
                )
            });
        }    

        return (
            <div className="flex-container">
                <Flex>
                    <Flex.Item className="text-center">
                        {defaultBaby.name}<br/><br/>
                        {defaultBaby.score} 金币
                    </Flex.Item>
                    <Flex.Item style={{flex: 2}}>
                        <img src={defaultBaby.avatar} alt="" width="100" height="100"/>
                    </Flex.Item>
                </Flex>
                <WhiteSpace size="lg" />
                <Flex>
                    <Flex.Item>
                        <WingBlank>
                            <Button type="primary" onClick={this.goToRecordHabbit}>为习惯打卡</Button>
                        </WingBlank>
                    </Flex.Item>
                    <Flex.Item>
                        <WingBlank>
                            <Button type="primary" onClick={this.goToCreateHabbit}>为宝贝建立习惯</Button>
                        </WingBlank>
                    </Flex.Item>
                </Flex>
                <WhiteSpace size="lg" />
                {habbitRecordListUI}
            </div>
        )
    }
}

const mapStateToProps = function(state) {
    return {
        defaultBaby: state.baby.defaultBaby,
        habbitRecordList: state.habbit.habbitRecordList
    }
}

const mapDispatchToProps = function(dispatch) {
    return {
        getDefaultBaby: () => dispatch(getDefaultBaby()),
        getHabbitRecord: (babyId) => dispatch(getHabbitRecord(babyId))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Dashboard);