import {addHabbitAPI, modifyHabbitAPI, getHabbitsAPI, getHabbitAPI, deleteHabbitAPI, 
    addHabbitRecordAPI, getHabbitRecordAPI} from '../../API/habbitAPI';
import {updateBabyScore} from '../../API/babyAPI';

export const addHabbit = (habbit) => {
    return async (dispatch) => {
        try {
            await addHabbitAPI(habbit);
            dispatch({type: 'CREAE_HABBIT_SUCCESS'});
            return true;
        } catch (err) {
            dispatch({type: 'CREATE_HABBIT_FAILED', err});
        }
    }
}

export const modifyHabbit = (habbit) => {
    return async (dispatch) => {
        try {
            await modifyHabbitAPI(habbit);
            dispatch({type: 'MODIFY_BABY_SUCCESS'});
            return true;
        } catch (err) {
            dispatch({type: 'MODIFY_BABY_FAILED', err});
        }
    }
}

export const deleteHabbit = (id, babyId) => {
    return async (dispatch, getState) => {
        try{
            const habbitList = await deleteHabbitAPI(id, babyId);
            dispatch({type: 'DELETE_HABBIT_SUCCESS', habbitList});
        } catch (err) {
            dispatch({type: 'DELETE_HABBIT_FAILED', err});
        }
    }
}

export const getHabbits = (babyId) => {
    return async (dispatch) => {
        try {
            const habbitList = await getHabbitsAPI(babyId);
            dispatch({type: 'GET_HABBITS_SUCCESS', habbitList});
        } catch (err) {
            dispatch({type: 'GET_HABBITS_FAILED', err});
        }
    }
}

export const getHabbit = (id) => {
    return async (dispatch) => {
        const habbit = await getHabbitAPI(id);
        dispatch({type: 'GET_HABBIT_SUCCESS', habbit});
        return habbit;
    }
}

export const addHabbitRecord = (habbit) => {
    return async (dispatch) => {
        try {
            await addHabbitRecordAPI(habbit);
            const babyId = habbit.babyId;
            const deltaScore = habbit.habbitType == 0 ? parseInt(habbit.reward) : (0 - parseInt(habbit.reward));
            await updateBabyScore(babyId, deltaScore);
            dispatch({type: 'CREAE_HABBIT_RECORD_SUCCESS'});
            return true;
        } catch (err) {
            dispatch({type: 'CREATE_HABBIT_RECORD_FAILED', err});
        }
    }
}

export const getHabbitRecord = (babyId) => {
    return async (dispatch) => {
        try {
            const habbitRecordList = await getHabbitRecordAPI(babyId);
            dispatch({type: 'GET_HABBIT_RECORD_LIST_SUCCESS', habbitRecordList});
        } catch (err) {
            dispatch({type: 'GET_HABBIT_RECORD_LIST_FAILED', err});
        }
    }
}