import React, { Component } from 'react';
import {BrowserRouter, Switch, Route, Link} from 'react-router-dom';
import SignIn from './components/auth/SignIn';
import SignUp from './components/auth/SignUp';
import './App.css';
import './API/initLeanCloud.js';
import {ConnectedAuthRoute, ConnectedUnauthRoute} from './components/auth/ConnectedRoute';
import Habbits from './components/habbit/Habbits';
import CreateHabbit from './components/habbit/CreateHabbit';
import BabyList from './components/baby/BabyList';
import BabyProfile from './components/baby/BabyProfile';
import Dashboard from './components/dashboard/Dashboard';
import HabbitRecord from './components/habbit/HabbitRecord';
import AddHabbitRecord from './components/habbit/AddHabbitRecord';
import {getCurrentUser} from './store/actions/authActions';
import {getDefaultBaby} from './store/actions/babyActions';
import {connect} from 'react-redux';
import TabNavBar from './components/dashboard/TabNavBar';
import ScoreStore from './components/score/ScoreStore';
import ProductForm from './components/score/ProductForm';

class App extends Component {
  state = {
    isLoaded: false
  };

  componentDidMount(){
    console.log("this.props.history", this.props.history)
    if(this.props.getCurrentUser) {
      const loggedInUser = this.props.getCurrentUser();
      this.setState({isLoaded: true});
      if(loggedInUser) {
        this.props.getDefaultBaby();
      }
    }
  }

  render() {
    if(!this.state.isLoaded)
      return (<div>Loading...</div>)

    return (  
        <BrowserRouter>
          <div>
            <Switch>
              <ConnectedAuthRoute path="/habbits" component={Habbits} redirectTo="/SignIn"/>
              <ConnectedAuthRoute path="/habbitRecord" component={HabbitRecord} redirectTo="/SignIn"/>
              <ConnectedAuthRoute path="/addHabbitRecord/:habbitId" component={AddHabbitRecord} redirectTo="/SignIn"/>
              <ConnectedAuthRoute path={["/CreateHabbit/:habbitId", "/CreateHabbit"]} component={CreateHabbit} redirectTo="/SignIn"/>
              <ConnectedAuthRoute path="/BabyList" component={BabyList} redirectTo="/SignIn"/>
              <ConnectedAuthRoute path="/BabyProfile/:babyId" component={BabyProfile} redirectTo="/SignIn"/>
              <ConnectedAuthRoute path="/Dashboard" component={Dashboard} redirectTo="/SignIn"/>
              <ConnectedAuthRoute path="/scoreExchange" component={ScoreStore} redirectTo="/SignIn"/>
              <ConnectedAuthRoute path={["/productForm/:productId", "/productForm/"]} component={ProductForm} redirectTo="/SignIn"/>
              <ConnectedUnauthRoute path="/SignIn" component={SignIn} redirectTo="/Dashboard"/>
              <ConnectedUnauthRoute path="/SignUp" component={SignUp} redirectTo="/Dashboard"/>
            </Switch>

            
            <TabNavBar></TabNavBar>
          </div>
        </BrowserRouter>   
    );
  }
}

const mapDispathToProps = (dispatch) => {
  return {
    getCurrentUser: () => dispatch(getCurrentUser()),
    getDefaultBaby: () => dispatch(getDefaultBaby())
  }
}

export default connect(null, mapDispathToProps)(App);
