import {Object, Query, User} from 'leancloud-storage';

const Habbit = Object.extend('Habbit');
const HabbitRecord = Object.extend('HabbitRecord');

export const addHabbitAPI = async (newHabbit) => {
    const currentUser = User.current();

    const habbit = new Habbit();
    habbit.set('description', newHabbit.description);
    habbit.set('habbitType', newHabbit.habbitType);
    habbit.set('hasClock', newHabbit.hasClock);
    habbit.set('reward', newHabbit.reward);
    habbit.set('babyId', newHabbit.babyId);
    habbit.set('owner', currentUser);
    return habbit.save();
}

export const modifyHabbitAPI = async (newHabbit) => {
    const habbit = Object.createWithoutData('Habbit', newHabbit.habbitId);

    habbit.set('description', newHabbit.description);
    habbit.set('habbitType', newHabbit.habbitType);
    habbit.set('hasClock', newHabbit.hasClock);
    habbit.set('reward', newHabbit.reward);
    habbit.set('babyId', newHabbit.babyId);
    return habbit.save();
}

export const deleteHabbitAPI = async (id, babyId) => {
    if(!id)
        return false;

    const habbit = Object.createWithoutData('Habbit', id);
    await habbit.destroy();
    return getHabbitsAPI(babyId)
}

export const getHabbitsAPI = async (babyId) => {
    const habbitQuery = new Query('Habbit');
    habbitQuery.include('description');
    habbitQuery.equalTo('babyId', babyId);
    habbitQuery.descending('createdAt');
    const habbits = await habbitQuery.find();
    return habbits.map(habbit => {
        return {
            id: habbit.id,
            description: habbit.get('description')
        }
    });
}

export const getHabbitAPI = async (id) => {
    if(!id)
        return false;
    const habbitQuery = new Query('Habbit');
    const habbit = await habbitQuery.get(id);
    return {
        habbitId: id,
        habbitType: habbit.get('habbitType'),
        description: habbit.get('description'),
        hasClock: habbit.get('hasClock'),
        reward: habbit.get('reward'),
        babyId: habbit.get('babyId')
    }
}

export const addHabbitRecordAPI = async (habbit) => {
    const currentUser = User.current();

    const habbitRecord = new HabbitRecord();
    habbitRecord.set('habbit', habbit);
    habbitRecord.set('babyId', habbit.babyId);
    habbitRecord.set('owner', currentUser);
    return habbitRecord.save();
}

export const getHabbitRecordAPI = async (babyId) => {
    if(!babyId)
        return false;
    const habbitRecordQuery = new Query('HabbitRecord');
    habbitRecordQuery.equalTo("babyId", babyId);
    habbitRecordQuery.include("habbit");
    habbitRecordQuery.include("createdAt");
    habbitRecordQuery.descending('createdAt');
    const habbitRecordList = await habbitRecordQuery.find();

    return habbitRecordList.map(record => {
        return {
            id: record.id,
            habbit: record.get('habbit'),
            createdAt: record.get('createdAt')
        }
    });
}