
const initState = {
    authError: null
}

const authReducer = (state = initState, action) => {
    switch(action.type) {
        case 'SIGN_UP_SUCCESS': 
            return {
                ...state,
                loggedInUser: action.loggedInUser,
                authError: null
            };
        case 'SIGN_UP_FAILED':
            return {
                ...state,
                authError: action.err
            };
        case 'SIGN_IN_SUCCESS': 
            return {
                ...state,
                loggedInUser: action.loggedInUser,
                authError: null
            };
        case 'SIGN_IN_FAILED':
            return {
                ...state,
                authError: action.err
            };
        case 'GET_CURRENT_USER':
            return {
                ...state,
                loggedInUser: action.loggedInUser
            };
        default:
            return state;
    }
}

export default authReducer;