import {Object, Query, User, File} from 'leancloud-storage';

const RewardProducts = Object.extend('RewardProducts');

export const addProductAPI = async (newProduct) => {
    const currentUser = User.current();

    const rewardProducts = new RewardProducts();
    rewardProducts.set('productName', newProduct.productName);
    rewardProducts.set('price', newProduct.price);
    rewardProducts.set('description', newProduct.description);
    rewardProducts.set('userId', currentUser.id);
    const file = newProduct.files[0].file;
    const fileName = file.name;
    const productCover = new File(fileName, file);
    rewardProducts.set('productCover', productCover);

    return rewardProducts.save();
}

export const modifyProductAPI = async (newProduct) => {
    const product = Object.createWithoutData('RewardProducts', newProduct.id);

    const file = newProduct.files[0].file;
    let productCover;
    if(file) {
        const fileName = file.name;
        productCover = new File(fileName, file);
    } else if (newProduct.files[0].url) 
        productCover = File.withURL(newProduct.files[0].name, newProduct.files[0].url);

    product.set('description', newProduct.description);
    product.set('productName', newProduct.productName);
    product.set('price', newProduct.price);
    product.set('productCover', productCover);
    return product.save();
}

export const deleteProductAPI = async (id, userId) => {
    if(!id)
        return false;

    const productQuery = Object.createWithoutData('RewardProducts', id);
    await productQuery.destroy();
    return getProductsAPI(userId);
}

export const getProductsAPI = async (userId) => {
    try{
        const productsQuery = new Query('RewardProducts');
        productsQuery.include('productName');
        productsQuery.include('price');
        productsQuery.include('description');
        productsQuery.equalTo('userId', userId);
        productsQuery.descending('createdAt');
        const products = await productsQuery.find();
        return products.map(product => {
            return {
                id: product.id,
                name: product.get('productName'),
                productCover: product.get('productCover') ? product.get('productCover').get('url') : null,
                price: product.get('price'),
                description: product.get('description')
            }
        });
    } catch(e) {
        return [];
    }
}

export const getProductAPI = async (id) => {
    if(!id)
        return false;
    const productQuery = new Query('RewardProducts');
    const product = await productQuery.get(id);
    return {
        id,
        productName: product.get('productName'),
        productCover: product.get('productCover'),
        price: product.get('price'),
        description: product.get('description')
    }
}