import React, { Component } from "react";
import EditableList from '../reuse/EditableList';
import {getProducts} from '../../store/actions/scoreActions';
import {connect} from 'react-redux';

class ScoreStore extends Component {
    handleProductImgClick(product, isEditMode) {
        console.log(product, isEditMode);
        if(isEditMode) {
            if (product.id) {
                this.props.history.push("/ProductForm/" + product.id); 
            } else  {
                this.props.history.push("/ProductForm");
            }
        } else {
            if (product.id) {
                this.props.history.push("/buyProduct");
            } else  {
                // If the "add baby" icon is clicked, go to add baby view
                this.props.history.push("/ProductForm");
            }
        }
    }

    render() {
        return (
            <div className="store-product-list">
                <EditableList dataList={this.props.products} getData={this.props.getProducts} 
                                handleImgClick={this.handleProductImgClick.bind(this)} pageTitle="积分商品"
                                imgPropName="productCover"></EditableList>
            </div>
        );
    }
}

const mapStateToProps = (state) => {
    return {
        products: state.product.productList
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        getProducts: () => dispatch(getProducts())
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(ScoreStore);