import React, {Component} from 'react';
import { List, NavBar, Icon } from 'antd-mobile';
import { Redirect } from 'react-router-dom';

import { IoIosAddCircle } from "react-icons/io"
import { MdDelete, MdDescription } from "react-icons/md";
import {getHabbits, deleteHabbit} from "../../store/actions/habbitsActions";
import {connect} from "react-redux";
import {Link} from 'react-router-dom';

const Item = List.Item;
const Brief = Item.Brief;

class Habbits extends Component {
    goToCreateHabbit = (e) => {
        this.props.history.push("/CreateHabbit");
    }

    goBack() {
        this.props.history.goBack();
    }

    componentDidMount() {
        if(this.props.getHabbits && this.props.defaultBabyId)
            this.props.getHabbits(this.props.defaultBabyId);
    }

    async handleDeleteHabbit(habbit) {
        await this.props.deleteHabbit(habbit.id, this.props.defaultBabyId);
    }

    render() {

        console.log("render")
        console.log(this.props.habbitList);

        // todo: is there's no baby, redirect to baby's home page
        //return <Redirect to="/BabyList"></Redirect>
        const {habbitList} = this.props;
        let habbitListUI;

        if(!habbitList || habbitList.length == 0)
            habbitListUI = (<div>There's no data available</div>);
        else    
            habbitListUI = habbitList.map(habbit => {
                    return (
                        <Item key={habbit.id} extra={ <div><MdDelete onClick={() => {this.handleDeleteHabbit(habbit)}}/><MdDescription/></div>} multipleLine align="top" wrap>
                            <Link to={"createHabbit/" + habbit.id}>{habbit.description}</Link>
                        </Item>
                    );
            });

        return (
            <div className="habbits with-nav-bar">
                <List renderHeader={() => <NavBar
                    mode="light"
                    icon={<Icon type="left" />}
                    onLeftClick={() => {this.goBack();}}
                    rightContent={ <IoIosAddCircle onClick={this.goToCreateHabbit}/> }
                    ></NavBar>} className="my-list">
                    {habbitListUI}
                </List>
            </div>
        )
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        getHabbits: (babyId) => dispatch(getHabbits(babyId)),
        deleteHabbit: (id, babyId) => dispatch(deleteHabbit(id, babyId))
    }
}

const mapStateToProps = (state) => {
    return {
        habbitList: state.habbit.habbitList,
        defaultBabyId: state.baby.defaultBaby && state.baby.defaultBaby.id
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Habbits);