import { connect } from 'react-redux'
import { AuthRoute, UnauthRoute } from 'react-router-auth'

const mapStateToProps = state => ({
  authenticated: state.auth.loggedInUser ? true: false, 
  // authenticated: true, 
})

export const ConnectedAuthRoute = connect(mapStateToProps)(AuthRoute);
export const ConnectedUnauthRoute = connect(mapStateToProps)(UnauthRoute);