import {Object, Query, User, File} from 'leancloud-storage';

const Baby = Object.extend('Baby');

export const createBaby = async (newBaby) => {
    const currentUser = User.current();

    const avatarFile = newBaby.files[0].file;
    const avatarFileName = avatarFile.name;
    const avatar = new File(avatarFileName, avatarFile);

    const baby = new Baby();
    baby.set('name', newBaby.babyName);
    baby.set('avatar', avatar);
    baby.set('score', 0);
    // baby.set('owner', currentUser);
    return baby.save();
}

export const modifyBaby = async (existBaby) => {
    const baby = Object.createWithoutData('Baby', existBaby.babyId);

    const avatarFile = existBaby.files[0].file;
    let avatar;
    if(avatarFile) {
        const avatarFileName = avatarFile.name;
        avatar = new File(avatarFileName, avatarFile);
    } else if (existBaby.files[0].url) 
        avatar = File.withURL(existBaby.files[0].name, existBaby.files[0].url);

    baby.set('name', existBaby.babyName);
    baby.set('avatar', avatar);
    return baby.save();
}

export const updateBabyScore = async (babyId, deltaScore) => {
    const baby = Object.createWithoutData('Baby', babyId);

    baby.increment("score", deltaScore);
    return baby.save();
}

export const getBabys = async () => {
    const babyQuery = new Query('Baby');
    babyQuery.include('name');
    babyQuery.include('avatar');
    babyQuery.descending('createdAt');
    const babys = await babyQuery.find();
    return babys.map(baby => {
        return {
            id: baby.id,
            name: baby.get('name'),
            avatar: baby.get('avatar') ? baby.get('avatar').get('url') : null,
            score: baby.get('score')
        }
    });
}

export const getBaby = async (id) => {
    if(!id)
        return false;
    const babyQuery = new Query('Baby');
    const baby = await babyQuery.get(id);
    return {
        id,
        babyName: baby.get('name'),
        avatar: baby.get('avatar'),
        score: baby.get('score')
    }
}

export const deleteBaby = async (id) => {
    if(!id)
        return false;

    const baby = Object.createWithoutData('Baby', id);
    return await baby.destroy();
}

export const setDefaultBaby = async (baby) => {
    if(!baby)
        return false;

    const currentUser = User.current();
    currentUser.set("defaultBaby", baby);
    return await currentUser.save();
}

export const getDefaultBaby = async () => {
    const currentUser = User.current();
    return await currentUser.get("defaultBaby");
}