import React, {Component} from 'react';
import {connect} from 'react-redux';
import EditableList from '../reuse/EditableList';
import {getBabys, setDefaultBaby} from '../../store/actions/babyActions';

class BabyList extends Component {

    async handleBabyImgClick(baby, isEditMode) {
        if(isEditMode) {
            // if has babyName, it's an exist baby, go to the baby's habbit home page
            if (baby.id) {
                this.props.history.push("/BabyProfile/" + baby.id); 
            } else  {
                // If the "add baby" icon is clicked, go to add baby view
                this.props.history.push("/BabyProfile/addNew");
            }
        } else {
            if (baby.id) {
                await this.props.setDefaultBaby(baby);
                this.props.history.push("/Dashboard");
            } else  {
                // If the "add baby" icon is clicked, go to add baby view
                this.props.history.push("/BabyProfile/addNew");
            }
        }
    }

    render() {
        return (
            <div className="baby-list">
                <EditableList dataList={this.props.babys} getData={this.props.getBabys} 
                                handleImgClick={this.handleBabyImgClick.bind(this)} pageTitle="我的宝贝"
                                imgPropName="avatar"></EditableList>
            </div>
        )
    }
}

const mapStateToProps = (state) => {
    return {
        babys: state.baby.babyList
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        getBabys: () => dispatch(getBabys()),
        setDefaultBaby: (baby) => dispatch(setDefaultBaby(baby))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(BabyList);