import React, {Component} from 'react';
import {connect} from 'react-redux';
import { List, InputItem, NavBar, Icon, ImagePicker, Button, WingBlank } from 'antd-mobile';
import { MdDone } from 'react-icons/md';
import {createBaby, getBaby, modifyBaby, deleteBaby} from '../../store/actions/babyActions';
const Item = List.Item;

class BabyProfile extends Component {
    state = {
        babyId: null,
        babyName: null,
        files: []
    }

    async componentDidMount() {
        if(this.props.match.params.babyId != "addNew")
            this.state.babyId = this.props.match.params.babyId;
        if(this.props.getBaby && this.state.babyId) {
            const baby = await this.props.getBaby(this.state.babyId);
            console.log(baby)
            this.setState({
                babyName: baby.babyName,
                files: baby.avatar ? [{
                    url: baby.avatar.get("url"),
                    name: baby.avatar.get("name")
                }] : []
            });
        }
    }

    async save() {
        // console.log("save", this.state);
        let isSuccess;
        if(this.state.babyId) {
            isSuccess = await this.props.modifyBaby(this.state);
        } else {
            isSuccess = await this.props.createBaby(this.state);
        }
        if(isSuccess)
            this.props.history.push('/BabyList');
    }

    goBack() {
        this.props.history.goBack();
    }

    onFileChange = (files, type, index) => {
        // console.log(files, type, index);
        this.setState({
            files
        });
    }

    onNameChange = (targetValue) => {
        this.setState({
            babyName: targetValue
        });
    }

    handleDeleteBaby = async (e) => {
        if(!this.state.babyId)
            return;

        const isSuccess = await this.props.deleteBaby(this.state.babyId);
        if(isSuccess)
            this.props.history.push("/babyList");
    }

    render() {

        return (
            <div className="baby-proile">
                <NavBar
                    mode="light"
                    icon={<Icon type="left" />}
                    onLeftClick={() => {this.goBack();}}
                    rightContent={ <MdDone onClick={() => {this.save();}}/> }
                    ></NavBar>
                
                <WingBlank>
                    <List>
                        <InputItem onChange={this.onNameChange} value={this.state.babyName} placeholder="宝宝名称"/>
                        <div>
                            <ImagePicker
                                files={this.state.files}
                                onChange={this.onFileChange}
                                onImageClick={(index, fs) => console.log(index, fs)}
                                selectable={this.state.files.length < 1}
                                multiple={false}
                                />
                        </div>                    
                    </List>
                    <div>
                        {this.state.babyId ? <Button onClick={this.handleDeleteBaby}>删除</Button> : ""}
                    </div>
                    <div className="form-error-msg">
                        {this.props.operationError ? this.props.operationError.rawMessage : null}
                    </div>
                </WingBlank>
            </div>
        );
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        createBaby: (baby) => dispatch(createBaby(baby)),
        modifyBaby: (baby) => dispatch(modifyBaby(baby)),
        getBaby: (id) => {
            console.log("map, getBaby", id);
            return dispatch(getBaby(id));
        },
        deleteBaby: (id) => dispatch(deleteBaby(id))
    }
}

export default connect(null, mapDispatchToProps)(BabyProfile);