import {signUpAPI, signInAPI, getCurrentUserAPI} from '../../API/authAPI';

export const signUp = (user) => {
    return async (dispatch, getState) => {
        // make acync call to database
        try {
            const loggedInUser = await signUpAPI(user);
            dispatch({type: 'SIGN_UP_SUCCESS', loggedInUser});
        } catch (err) {
            dispatch({type: 'SIGN_UP_FAILED', err});
        }
    }
}

export const signIn = (user) => {
    return async (dispatch, getState) => {
        try {
            const loggedInUser = await signInAPI(user);
            dispatch({type: 'SIGN_IN_SUCCESS', loggedInUser});
        } catch (err) {
            dispatch({type: 'SIGN_IN_FAILED', err});
        }
    }
}

export const getCurrentUser = () => {
    return (dispatch) => {
        const currentUser = getCurrentUserAPI();
        const loggedInUser = (currentUser && !currentUser.isAnonymous() ? currentUser : null)
        dispatch({type: 'GET_CURRENT_USER', loggedInUser: loggedInUser});
        return loggedInUser;
    }
}