import {createBaby as createBabyAPI, 
    getBabys as getBabysAPI, 
    getBaby as getBabyAPI,
    modifyBaby as modifyBabyAPI,
    setDefaultBaby as setDefaultBabyAPI,
    getDefaultBaby as getDefaultBabyAPI,
    deleteBaby as deleteBabyAPI} from '../../API/babyAPI';

export const createBaby = (baby) => {
    return async (dispatch, getState) => {
        try {
            await createBabyAPI(baby);
            dispatch({type: 'CREAE_BABY_SUCCESS'});
            return true;
        } catch (err) {
            dispatch({type: 'CREATE_BABY_FAILED', err});
        }
    }
}

export const modifyBaby = (baby) => {
    return async (dispatch, getState) => {
        try {
            await modifyBabyAPI(baby);
            dispatch({type: 'MODIFY_BABY_SUCCESS'});
            return true;
        } catch (err) {
            dispatch({type: 'MODIFY_BABY_FAILED', err});
        }
    }
}

export const deleteBaby = (babyId) => {
    return async (dispatch, getState) => {
        try{
            await deleteBabyAPI(babyId);
            dispatch({type: 'DELETE_BABY_SUCCESS'});
            return true;
        } catch (err) {
            dispatch({type: 'DELETE_BABY_FAILED', err});
        }
    }
}

export const setDefaultBaby = (baby) => {
    return async (dispatch, getState) => {
        try {
            await setDefaultBabyAPI(baby);
            return true;
        } catch (err) {
            console.log("failed to set default baby", err);
        }
    }
}

export const getDefaultBaby = () => {
    return async (dispatch, getState) => {
        try {
            const defaultBaby = await getDefaultBabyAPI();
            dispatch({type: 'GET_DEFAULT_BABY', defaultBaby});
            return defaultBaby;
        } catch (err) {
            console.log("failed to get default baby", err);
        }
    }
}

export const getBabys = () => {
    return async (dispatch) => {
        const babyList = await getBabysAPI();
        dispatch({type: 'GET_BABYS_SUCCESS', babyList});
    }
}

export const getBaby = (id) => {
    return async (dispatch) => {
        const baby = await getBabyAPI(id);
        dispatch({type: 'GET_BABY_SUCCESS', baby});
        return baby;
    }
}