const initState = {
    operationError: null,
    babyList: null
}

const babyReducer = (state = initState, action) => {
    switch(action.type) {
        case 'CREATE_BABY_SUCCESS':
            return state;
        case 'CREATE_BABY_FAILED':
            return {
                ...state,
                operationError: action.err
            }
        case 'MODIFY_BABY_FAILED':
        return {
            ...state,
            operationError: action.err
        }    
        case 'GET_BABYS_SUCCESS':
            return {
                ...state,
                babyList: action.babyList
            }
        case 'GET_BABY_SUCCESS':
            return {
                ...state,
                baby: action.baby
            }
        case 'GET_DEFAULT_BABY':
            return {
                ...state,
                defaultBaby: action.defaultBaby
            }
        default:
            return state;
    }
}

export default babyReducer;