import React, {Component} from 'react';

import { getHabbit } from '../../store/actions/habbitsActions';
import { connect } from 'react-redux';
import { WingBlank, WhiteSpace, NavBar, Icon } from 'antd-mobile';
import {IoIosThumbsUp, IoIosThumbsDown} from 'react-icons/io';
import {addHabbitRecord} from '../../store/actions/habbitsActions';

class AddHabbitRecord extends Component {
    state = {
        habbitId: null,
        habbitType: 0,
        description: '',
        hasClock: false,
        reward: "5",
        babyId: null
    }

    goBack() {
        this.props.history.goBack();
    }

    async componentDidMount() {
        if(this.props.match.params && this.props.getHabbit) {
            const habbitId = this.props.match.params.habbitId;
            const habbit = await this.props.getHabbit(habbitId);
            this.setState({
                ...habbit
            });
        }
               
    }

    async handleScoreAction() {
        const isSucess = await this.props.addHabbitRecord(this.state);
        if(isSucess)
            this.props.history.push("/dashboard");
    }

    render() {

        return (
            <div className="add-habbit-record">
                <NavBar
                    mode="light"
                    icon={<Icon type="left" />}
                    onLeftClick={() => {this.goBack();}}
                    ></NavBar>

                <WingBlank>
                    <div className="habbit-desc">{this.state.description}</div>
                </WingBlank>

                <WhiteSpace size="lg" />
                <WingBlank size="md">
                    <div className="score-action" onClick={()=>{this.handleScoreAction();}}>  
                    {
                        this.state.habbitType == 0 ? <IoIosThumbsUp></IoIosThumbsUp>
                                                          : <IoIosThumbsDown></IoIosThumbsDown>
                    }
                    </div>
                </WingBlank>

                <WhiteSpace size="lg" />
                <WingBlank size="sm">
                </WingBlank>
            </div>
        )
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        getHabbit: (id) => dispatch(getHabbit(id)),
        addHabbitRecord: (habbit) => dispatch(addHabbitRecord(habbit))
    }
}

const mapStateToProps = (state) => {
    return {
        defaultBaby: state.baby.defaultBaby,
        habbit: state.habbit.habbit
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(AddHabbitRecord);