const initState = {
    operationError: null
}

const habbitReducer = (state = initState, action) => {
    switch(action.type) {
        case 'CREAE_HABBIT_SUCCESS':
            return state
        case 'CREATE_HABBIT_FAILED':
            return {
                ...state,
                operationError: action.err
            }
        case 'GET_HABBITS_SUCCESS':
            return {
                ...state,
                habbitList: action.habbitList
            }
        case 'GET_HABBIT_SUCCESS':
            return {
                ...state,
                habbit: action.habbit
            }
        case 'DELETE_HABBIT_SUCCESS':
            return {
                ...state,
                habbitList: action.habbitList
            }
        case 'GET_HABBIT_RECORD_LIST_SUCCESS':
            return {
                ...state,
                habbitRecordList: action.habbitRecordList
            }
        default:
            return state;    
    }
}

export default habbitReducer;