import React, {Component} from 'react';
import {connect} from 'react-redux';
import {getHabbits} from "../../store/actions/habbitsActions";
import {List, NavBar, Icon} from 'antd-mobile';
const Item = List.Item;
const Brief = Item.Brief;

class HabbitRecord extends Component {
    componentDidMount() {
        if(this.props.getHabbits && this.props.defaultBabyId)
            this.props.getHabbits(this.props.defaultBabyId);
    }

    goBack() {
        this.props.history.goBack();
    }

    render() {
        const {habbitList} = this.props;
        let habbitUI;
        if(!habbitList || !habbitList.length)
            habbitUI = <div onClick={() => this.props.history.push("/CreateHabbit")}>还没有任何习惯哦，点这里为宝贝建立习惯。</div>
        else
            habbitUI = habbitList.map(habbit => <Item arrow="horizontal" key={habbit.id} onClick={() => {this.props.history.push("/addHabbitRecord/" + habbit.id)}}>{habbit.description}</Item>)
        return (
            <div className="habbit-record with-nav-bar">
                <List renderHeader={() => <NavBar
                    mode="light"
                    icon={<Icon type="left" />}
                    onLeftClick={() => {this.goBack();}}
                    rightContent={ '请选择习惯' }
                    ></NavBar>} className="my-list">
                    {
                        habbitUI
                    }
                </List>
            </div>
        )
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        getHabbits: (babyId) => dispatch(getHabbits(babyId))
    }
}

const mapStateToProps = (state) => {
    return {
        habbitList: state.habbit.habbitList,
        defaultBabyId: state.baby.defaultBaby && state.baby.defaultBaby.id
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(HabbitRecord);