import React, {Component} from 'react';
import { TabBar } from 'antd-mobile';
import { MdHome, MdGroup, MdStore } from "react-icons/md";
import {Link} from 'react-router-dom';

class TabNavBar extends Component {

    state = {
       selectedTab: 'redTab',
    }

    renderIcon(page, isSelected) {
        const links = {
            "home": "/Dashboard",
            "babys": "/BabyList",
            "scoreExchange": "/scoreExchange"
        }

        let icon = "";
        if(page == 'home')
            icon = <MdHome></MdHome>;
        else if(page == "babys")
            icon = <MdGroup></MdGroup>
        else if (page == "scoreExchange")
            icon = <MdStore></MdStore>

        return <Link to={links[page]}>
                    <div className={"tab-icon " + (isSelected ? "selected" : "")}>
                        {icon}
                    </div>
                </Link>
    }

    render() {
        
        return (
            <div className="tab-nav-bar">
                <TabBar
                    unselectedTintColor="#949494"
                    tintColor="#33A3F4"
                    barTintColor="white"
                    hidden={false}
                >
                    
                    <TabBar.Item
                    title="首页"
                    key="home"
                    icon={
                        this.renderIcon('home')
                    }
                    selectedIcon={
                        this.renderIcon('home', true)
                    }
                    selected={this.state.selectedTab === 'home'}
                    onPress={() => {
                        this.setState({
                        selectedTab: 'home',
                        });
                    }}
                    >
                    </TabBar.Item>
                    
                    <TabBar.Item
                    icon={
                        this.renderIcon('babys')
                    }
                    selectedIcon={
                        this.renderIcon('babys', true)
                    }
                    title="宝宝"
                    key="baby"
                    selected={this.state.selectedTab === 'babys'}
                    onPress={() => {
                        this.setState({
                        selectedTab: 'babys',
                        });
                    }}
                    >                    
                    </TabBar.Item>

                    <TabBar.Item
                    icon={
                        this.renderIcon('scoreExchange')
                    }
                    selectedIcon={
                        this.renderIcon('scoreExchange', true)
                    }
                    title="金币兑换"
                    key="scoreExchange"
                    selected={this.state.selectedTab === 'scoreExchange'}
                    onPress={() => {
                        this.setState({
                        selectedTab: 'scoreExchange',
                        });
                    }}
                    >                    
                    </TabBar.Item>
                    
                </TabBar>
                </div>
        );
    }
}

export default TabNavBar;
