import React, {Component} from 'react';
import addBabyIcon from '../../imgs/add-baby-icon.png';
import { Grid } from 'antd-mobile';

class EditableList extends Component {
    state = {
        isEditMode: false
    }

    componentDidMount() {
        this.isFirstLoad = true;
        if(this.props.getData)
            this.props.getData();
    }    

    async handleImgClick(item) {
        this.props.handleImgClick(item, this.state.isEditMode);
    }

    switchMode () {
        if(this.state.isEditMode)
            this.setState({
                isEditMode: false
            });
        else
            this.setState({
                isEditMode: true
            });
    }
    
    render() {
        const {dataList, pageTitle, imgPropName} = this.props;
        if(!dataList)
            return (
                <div>Loading...</div>
            )

        if(this.isFirstLoad) {
            dataList.push({[imgPropName]: addBabyIcon});
            //this.props.babys = babys;
        }

        this.isFirstLoad = false;
        // this.data1.push({icon: addBabyIcon});
        return (
            <div className="editable-list">
                <div className="sub-title component-header">
                    <div>{pageTitle}</div>
                    <div className="clickable" onClick={() => this.switchMode()}>
                        {this.state.isEditMode ? '完成' : '编辑'}
                    </div>
                </div>
                <Grid data={dataList}
                columnNum={2}  hasLine={false}
                renderItem={dataItem => (
                    <div style={{ padding: '12.5px' }}>
                    <img onClick={() => this.handleImgClick(dataItem)} src={dataItem[imgPropName]} style={{ width: '75px' }} alt="" />
                    <div style={{ color: '#888', fontSize: '14px', marginTop: '12px' }}>
                        <span>{dataItem.name ? dataItem.name : null}</span>
                    </div>
                    </div>
                )}
                />
            </div>
        );
    }
}

export default EditableList;