import React, {Component} from 'react';
import { List, TextareaItem, NavBar, Icon, Radio, InputItem, Switch } from 'antd-mobile';
import { MdDone } from 'react-icons/md';
import { addHabbit, modifyHabbit, getHabbit } from '../../store/actions/habbitsActions';
import { connect } from 'react-redux';

const RadioItem = Radio.RadioItem;

class CreateHabbit extends Component {
    state = {
        habbitId: null,
        habbitType: 0,
        description: '',
        hasClock: false,
        reward: "5",
        babyId: null
    }

    async componentDidMount() {
        if(this.props.match.params && this.props.getHabbit) {
            const habbitId = this.props.match.params.habbitId;
            const habbit = await this.props.getHabbit(habbitId);
            this.setState({
                ...habbit
            });
        }
               
    }

    goBack() {
        this.props.history.goBack();
    }

    save() {
        this.setState({
            babyId: this.props.defaultBaby.id
        },
        async () => {
            let isSuccess;
            if(this.state.habbitId) {
                isSuccess = await this.props.modifyHabbit(this.state);
            } else
                isSuccess = await this.props.addHabbit(this.state);

            if(isSuccess)
                this.props.history.push("/habbits");
        }); 
        
    }

    onHabbitTypeChange(v) {
        this.setState({
            habbitType: v
        });
    }

    onDescriptionChange(v) {
        this.setState({
            description: v
        });
    }

    onRewardChange(v) {
        this.setState({
            reward: v
        });
    }

    render() {
        return (
            <div className="habbits with-nav-bar">
                <List renderHeader={<NavBar
                        mode="light"
                        icon={<Icon type="left" />}
                        onLeftClick={() => {this.goBack();}}
                        rightContent={ <MdDone onClick={() => {this.save();}}/> }
                        ></NavBar>} className="my-list">
                    <List.Item>
                        <TextareaItem
                        rows={5}
                        count={100}
                        placeholder="习惯描述"
                        value={this.state.description}
                        onChange={(v) => this.onDescriptionChange(v)}
                        />
                    </List.Item>    
                    <List.Item>
                        <RadioItem key="0" checked={0 === this.state.habbitType} onChange={() => this.onHabbitTypeChange(0)}>
                            好习惯
                        </RadioItem>
                        <RadioItem key="1" checked={1 === this.state.habbitType} onChange={() => this.onHabbitTypeChange(1)}>
                            坏习惯
                        </RadioItem>
                    </List.Item>  
                    <List.Item>
                        闹钟<Switch
                            checked={this.state.hasClock}
                            onChange={() => {
                                this.setState({
                                    hasClock: !this.state.hasClock,
                                });
                            }}
                        />
                    </List.Item>
                    <List.Item>
                        <InputItem type="digit" 
                            value={this.state.reward} extra="金币" onChange={(v) => this.onRewardChange(v)}>奖励</InputItem>
                    </List.Item>
                </List>
            </div>
        );
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        addHabbit: (habbit) => dispatch(addHabbit(habbit)),
        modifyHabbit: (habbit) => dispatch(modifyHabbit(habbit)),
        getHabbit: (id) => dispatch(getHabbit(id)),

    }
}

const mapStateToProps = (state) => {
    return {
        defaultBaby: state.baby.defaultBaby,
        habbit: state.habbit.habbit
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(CreateHabbit);