import React, {Component} from 'react';
import {connect} from 'react-redux';
import { List, InputItem, NavBar, Icon, ImagePicker, Button, TextareaItem, Stepper } from 'antd-mobile';
import { MdDone } from 'react-icons/md';
import {addProduct, getProduct, modifyProduct, deleteProduct} from '../../store/actions/scoreActions';
const Item = List.Item;

class ProductForm extends Component {
    state = {
        productId: null,
        productName: null,
        price: 5,
        description: null,
        files: []
    }

    async componentDidMount() {
        if(this.props.match.params.productId != "addNew")
            this.state.productId = this.props.match.params.productId;
        if(this.props.getProduct && this.state.productId) {
            const product = await this.props.getProduct(this.state.productId);
            console.log(product)
            this.setState({
                productName: product.productName,
                files: product.productCover ? [{
                    url: product.productCover.get("url"),
                    name: product.productCover.get("name")
                }] : []
            });
        }
    }

    async save() {
        console.log("save", this.state);
        let isSuccess;
        if(!this.state.productId) {
            isSuccess = await this.props.addProduct(this.state);
        } else {
            isSuccess = await this.props.modifyProduct(this.state);
        }
        if(isSuccess)
            this.props.history.push('/scoreExchange');
    }

    goBack() {
        this.props.history.goBack();
    }

    onFileChange = (files, type, index) => {
        // console.log(files, type, index);
        this.setState({
            files
        });
    }

    onNameChange = (targetValue) => {
        this.setState({
            productName: targetValue
        });
    }

    onPriceChange = (price) => {
        this.setState({ price });
    }

    onDescriptionChange = (description) => {
        this.setState({ description });

    }

    handleDeleteProduct = async (e) => {
        if(!this.state.productId)
            return;

        const isSuccess = await this.props.deleteProduct(this.state.productId);
        if(isSuccess)
            this.props.history.push("/scoreExchange");
    }

    render() {
        return (
            <div className="product-form">
                <NavBar
                    mode="light"
                    icon={<Icon type="left" />}
                    onLeftClick={() => {this.goBack();}}
                    rightContent={ <MdDone onClick={() => {this.save();}}/> }
                    ></NavBar>
                
                <div>
                    <List>
                        <InputItem onChange={this.onNameChange} value={this.state.productName} placeholder="名称"/>
                        <List.Item
                            wrap
                            extra={"金币"
                                }
                            >
                            <Stepper
                                style={{ width: '100%', minWidth: '100px' }}
                                showNumber
                                value={this.state.price}
                                onChange={this.onPriceChange}
                                />
                        </List.Item>
                        <TextareaItem
                            placeholder="请写下该商品的描述"
                            rows={5}
                            count={100}
                            onChange={this.onDescriptionChange}
                        />
                        <div>
                            <ImagePicker
                                files={this.state.files}
                                onChange={this.onFileChange}
                                onImageClick={(index, fs) => console.log(index, fs)}
                                selectable={this.state.files.length < 1}
                                multiple={false}
                                />
                        </div>                    
                    </List>
                    <div>
                        {this.state.productId ? <Button onClick={this.handleDeleteProduct}>删除</Button> : ""}
                    </div>
                    <div className="form-error-msg">
                        {this.props.operationError ? this.props.operationError.rawMessage : null}
                    </div>
                </div>
            </div>
        );
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        addProduct: (product) => dispatch(addProduct(product)),
        modifyProduct: (product) => dispatch(modifyProduct(product)),
        getProduct: (id) => {
            console.log("map, getProduct", id);
            return dispatch(getProduct(id));
        },
        deleteProduct: (id) => dispatch(deleteProduct(id))
    }
}

const mapStateToProps = (state) => {
    return {
        operationError: state.product.operationError
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(ProductForm);